'use client';

import React, { useState } from 'react';
import { Section, Container, Text, Input, Textarea } from '../atoms';
import { Send } from 'lucide-react';
import styles from './Contact.module.css';

export interface ContactProps {
  sectionNumber?: string;
  headline?: string;
  description?: string;
}

export const Contact: React.FC<ContactProps> = ({
  sectionNumber = '04',
  headline = "Let's Build Something Together",
  description = 'Open to collaboration on research projects, open-source contributions, healthcare AI initiatives, or consulting opportunities.',
}) => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    message: '',
  });

  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);
    
    // TODO: Implement form submission logic
    console.log('Form submitted:', formData);
    
    // Simulate API call
    setTimeout(() => {
      setIsSubmitting(false);
      setFormData({ name: '', email: '', message: '' });
      alert('Message sent successfully!');
    }, 1000);
  };

  return (
    <Section id="contact" className={styles.contact}>
      <Container>
        <div className={styles.contactWrapper}>
          <div className={styles.contactHeader}>
            <div className={styles.contactHeaderContent}>
              <div className={styles.contactTitle}>
                <Text variant="h2" className={styles.sectionNumber}>
                  {sectionNumber}
                </Text>
                <Text variant="h2">{headline}</Text>
              </div>
              <Text variant="bodyLarge" color="secondary" className={styles.contactDescription}>
                {description}
              </Text>
            </div>
            <div className={styles.contactVisual}>
              <div className={styles.cursorIndicator}>
                <div className={styles.cursorDot} />
              </div>
            </div>
          </div>

          <div className={styles.contactFormSection}>
            <form className={styles.contactForm} onSubmit={handleSubmit}>
              <Input
                label="NAME"
                name="name"
                value={formData.name}
                onChange={handleChange}
                required
                placeholder=""
              />
              <Input
                label="EMAIL"
                name="email"
                type="email"
                value={formData.email}
                onChange={handleChange}
                required
                placeholder=""
              />
              <Textarea
                label="MESSAGE"
                name="message"
                value={formData.message}
                onChange={handleChange}
                required
                placeholder=""
              />
              <div className={styles.formActions}>
                <button
                  type="submit"
                  className={styles.sendButton}
                  disabled={isSubmitting}
                >
                  {isSubmitting ? 'Sending...' : 'Send Message'}
                  <Send size={16} />
                </button>
              </div>
            </form>
            <div className={styles.contactFormVisual}>
              <div className={styles.cursorIndicator}>
                <div className={styles.cursorDot} />
              </div>
            </div>
          </div>
        </div>
      </Container>
    </Section>
  );
};

